<template>
  <div class="showContent companyInfo">
    <requestAndHisSubMenu :routekey="routekey"></requestAndHisSubMenu>
    <div class="box_request">
      <h3>Upload photos</h3>
      <uploadImage ref="uploadImage" :showNum="1" class="marginleft"></uploadImage>
      <div class="box_line"></div>
      <div
        class="image_info"
      >Upload photos or just drag and drop face comparison picture here add 2 photos, JPG/JPEG/PNG ; 256*256 ~ 4096*4096, <2MB</div>
      <div class="image_submit">
        <a-button @click="gotoClear">Reset</a-button>
        <a-button type="primary" @click="gotoResult" htmlType="submit">Check</a-button>
      </div>
    </div>
    
    <!-- <p class="result">Result</p> -->
    <!-- <div class="box">
      <div class="infor_form">
        <div>
          <label>Similar</label>
          <a-progress :percent="percentNum"/>
        </div>
      </div>
    </div> -->
    <section class="result" id="result">
      <p>Result</p>
      <div class="box_result">
        <div class="box_title">Similar</div>
        <progressBar :present="percentNum" class="marginleft"></progressBar>
        <div class="box_line"></div>
        <div class="box_info" v-if="scoreFlag=='A' ||scoreFlag=='B' ">
          <div class="box_icon"><img src="../../../assets/image/cup.png" alt=""></div>
          <label>Congratulations</label>
          <label>You get a hign score</label>
        </div>
        <div class="box_info" v-if="scoreFlag=='C' || scoreFlag=='D'">
          <div class="box_icon"><img src="../../../assets/image/cup.png" alt=""></div>
          <label>Bad</label>
          <label>You get a low score</label>
        </div>
        
      </div>
    </section>
  </div>
</template>
<script>
import requestAndHisSubMenu from "@/components/requestAndHisSubMenu";
import uploadImage from "@/components/uploadImage/uploadImage";
import progressBar from "@/components/progressBar";
export default {
  data() {
    return {
      routekey: ["idHoldingRequest", "idHoldingHistory;idHoldingHistoryDetail"],
      formLayout: "inline",
      percentNum: 0,
    };
  },
  components: {
    requestAndHisSubMenu,
    uploadImage,
    progressBar
  },
  computed:{
    scoreFlag(){
      if(this.percentNum>=0 && this.percentNum<40){   //0-40
          return 'D';
      }else if(this.percentNum>=40 && this.percentNum<60){//40-60
          return 'C';
      }else if(this.percentNum>=60 && this.percentNum<80){//60-80
          return 'B';
      }else{
          return 'A';
      }
    }
  },
  methods: {
    gotoClear() {
      this.$refs.uploadImage.deleteImage(0);
      this.$util.showOrHideDiv("result", false);
    },
    gotoResult() {
      this.fileObj = this.$refs.uploadImage.getImageFile();
      console.log("gotoResult", this.fileObj);
      if (this.fileObj.fileImage) {
        if (this.fileObj.errImg == true) {
          this.$message.error(this.$t("verification.errorImg"));
        } else {
          console.log("gotoResult", this.fileObj.fileImage);
          let params = {
            image_file_1: this.fileObj.fileImage
          };
          this.$store.dispatch("holdingCard", params).then(
            res => {
              console.log("holdingCard res", res);
              let percent = res.data.score ? res.data.score : 0;
              this.percentNum = percent
                ? Number.parseInt(percent.replace("%", ""))
                : 0;
              this.$util.showOrHideDiv("result", true);
            },
            error => {
              console.log("holdingCard error", error);
              if(error.code==400) this.$message.error('检测失败');
            }
          );
        }
      } else {
        this.$message.error(this.$t("main.pleaseUploadPicture"));
      }
    }
  },
  mounted() {
    this.$util.showOrHideDiv("result", false);
  }
};
</script>
<style lang="less">
@import "../../../css/page.less";
</style>



